@import "../../VariablesScss/variables";

.gallery {
  background: url("/assets/Banner/layer-14-copy@3x.png"),
    linear-gradient(90deg, rgba(75, 68, 255, 1) 50%, rgba(52, 191, 243, 1) 100%);
  background-size: cover;
  background-position: center/top;
  width: 50%;
  .dots {
    list-style-type: none;
    display: flex;
    justify-content: center;
    padding: 0;
  }
  .dot {
    background-color: $color-white;
    opacity: 0.3;
    width: 35px;
    height: 5px;
    margin: 10px;
    &:hover {
      opacity: 1;
    }
  }
  .slider {
    transform: translateY(30%);
    div {
      outline: none;
    }
  }
  .card {
    .imgContainer {
      // width: 100%;
      height: 22rem;
      // object-fit: fill;
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
      display: flex;
      align-items: center;
      justify-content: center;
      .mobileCover {
        position: relative;
        transform: translateY(0.8rem);
        border-radius: 10px;
        // width: 60%;
        height: 90%;
        object-fit: contain;
        display: block;
        overflow: hidden;
      }
      .mobileScreen {
        overflow: hidden;
        position: absolute;
        border-radius: 10px;
        height: 60%;
        width: 200px;
        object-fit: cover;
        object-position: top;
        display: block;
      }
    }
    h3 {
      color: $color-white;
      font-weight: 400;
      text-align: center;
      margin: 1rem auto;
      width: 40%;
      // width: 60%;
      span {
        font-weight: bold;
      }
    }
    p {
      text-align: center;
      color: $color-white;
      width: 65%;
      margin: 0 auto;
    }
  }
  @media screen and (max-width: 1000px) {
    width: 100%;
    height: 700px;
    padding: 0 0 3rem 0;

    .mobileScreen {
      transform: translateY(15px);
    }
  }
}
