@import "../../VariablesScss/variables";

.HeyNavTheme {
  display: flex;
  width: 100%;
  margin: 4rem auto;
  .description {
    h2 {
      font-size: 1.9rem;
      font-weight: 500;
      margin: 0.5rem 0;
      span {
        color: $color-txt-7;
        font-weight: bold;
      }
    }
    .subtitle {
      font-weight: bolder;
      text-transform: capitalize;
      display: flex;
      align-items: center;
      font-size: 1.2rem;
      img {
        width: 2rem;
        margin: auto 1rem;
      }
    }
    p {
      color: $color-txt-3;
      width: 85%;
      margin: 10px 0;
    }
    .list {
      padding: 0;
      list-style: none;
      margin: 0;
      li {
        font-weight: 600;
        display: flex;
        align-items: center;
        margin: 1rem 0;
        img {
          width: 30px;
          height: 30px;
          // margin-left: 12px;
          margin-right: 15px;
          object-fit: contain;
        }
        .left {
          width: 40px;
          height: 40px;
        }
        span {
          margin-right: 10px;
        }
      }
    }
    .magic {
      display: flex;
      align-items: center;
      p {
        color: $color-txt-6;
        width: 80%;
        margin-right: 1rem;

        span {
          color: $color-txt-5;
          font-weight: bold;
        }
      }
      svg {
        width: 2rem;
        height: 2rem;
        margin-right: 1rem;
      }
    }
  }
  .imgContainer {
    margin: auto;
    width: 300px;
    height: 300px;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    justify-content: center;
    .mobileCover {
      position: relative;
      transform: translateY(1rem);
      border-radius: 10px;
      width: 90%;
      height: 100%;
      object-fit: contain;
      display: block;
    }
    .mobileScreen {
      position: absolute;
      transform: translateY(3.5rem);
      border-radius: 10px;
      // width: 100%;
      height: 350px;
      object-fit: cover;
      display: block;
    }
  }
  @media screen and (max-width: 800px) {
    flex-direction: column;
    width: 90%;
    .description {
      display: flex;
      flex-direction: column;
      order: 2;

      .magic {
        p {
          width: 100%;
        }
      }
    }
    .imgContainer {
      margin-bottom: 2rem;
      .mobileCover {
        width: 250px;
      }
      .mobileScreen {
        height: 320px;
        transform: translateY(2.5rem);
      }
    }
  }
}
